import React, { Component } from 'react'
import a1 from './photos/1.png';
import a2 from './photos/2.png';
import a3 from './photos/3.png';
import a4 from './photos/4.png';
import a5 from './photos/5.png';
import a6 from './photos/6.png';
import a7 from './photos/7.png';
import a8 from './photos/8.png';
import a9 from './photos/9.png';
import a10 from './photos/10.png';


export default class PhotoDisplay extends Component {
    images = [
        a1,
        a2,
        a3,
        a4,
        a5,
        a6,
        a7,
        a8,
        a9,
        a10
    ];


    
    
    render() {
        return (
           <img src={this.images[Math.floor(Math.random() * this.images.length)]}/>
        )
    }
}