import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import "./DailyQuote.css"

export default class DailyQuote extends Component {
    quotes = [
        "Among Us? @everyone",
        "oh my noah... nevermind",
        "wanna join us in vc after school?",
        "leave or sleep",
        "i thought once gold was diamond",
        "and then we started talking about Auschwitz",
        "what? >:(",
        "White Guy? Murder? Butter?",
        "from 69 hours to 70 hours today",
        "Farming Simulator 19 AFK Grind",
        "still laughing that you wasted money on uno",
        "Computers/programing is elis",
        "25 years in prison"
    ];




    render() {
        var randomQuote = "**\"**"+ this.quotes[Math.floor(Math.random() * this.quotes.length)] + "**\"**" + " - *DDennis235* ";
            
//dennis1




        return (
            <div>
                <ReactMarkdown source= {randomQuote} />
            </div>
        )
    }
}
